import Header from "./Header.js";
import "../landing/Index.css";
import React from 'react'
import { Grid } from 'semantic-ui-react'
import graphicsLeft from "../../graphics/intro-graphic-left.svg";
import graphicsRight from "../../graphics/intro-graphic-right.svg";
function Index() {
  return (
    <div className="Index">
      <Grid textAlign="right" columns={3}>
        <Grid.Row>
          <Grid.Column>
          <img className="" src={graphicsLeft} alt="background"></img>
          </Grid.Column>
          <Grid.Column>
          <img className="" src={graphicsRight} alt="background"></img>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </div>
  );
}

export default Index;
